var keyboardTimeout;
var searchTimeout;
$(function() {
  var test = document.createElement("div");
  test.id="fontTest";
  test.style.width="calc(100% - 60px)";
  test.style.height="100px";
  test.style.whiteSpace="nowrap";
  test.innerHTML="<span></span>";
  document.body.appendChild(test);
  // $("input.station").bind("touchstart",function() { $(this).focus(); });
  $(test).css("-webkit-transform","translateY(-3000px)");
  $(test).textfill({
     maxFontPixels: 80
  });
  $("input.station").bind("keyup",update);
  function update(e) {
    clearTimeout(keyboardTimeout);
    var value = $("input.station").val();
    if (value=="") value="a";
    $("#fontTest").html("<span>"+value+"_</span>");
    $("#fontTest").delay(20).textfill({
       maxFontPixels: 80
    });
    $("input.station").css("font-size",$("#fontTest span").css("font-size"));
    stationSearch(value);
  }
  function stationSearch(value) {
    $("ul.list").html("<li><span class=\"searching\"></span></li>");
    clearTimeout(searchTimeout);
    searchTimeout = setTimeout(function() {
      $("ul.list").css("opacity",0).html('<li style="opacity:0" class="clickaction">Vauxhall</li><li style="opacity:0" class="remove" class="clickaction">Waterloo</li>');
      $("ul.list").animate({ opacity: 1 },300);
      $("ul.list li").each(function(i,e) {
        setTimeout(function(obj) {
          $(obj).animate({ opacity: 1 },300);
        },i*200+200,e);
      }).bind("click",function() {
        closeDestinationScreen();
        enterHomeScreen();
      });
    }, 1000);
    keyboardTimeout = setTimeout(removeKeyboardFocus,1500);
  }
  function removeKeyboardFocus() {
    $("input.station").blur();
  }
});
