$(function() {
	 enterSplash();
	 // enterHomeScreen();
	 // $(".controls").css("opacity",1);
	function enterSplash() {
		$(".splash.screen").css("display","block").css("opacity",1);
		stepAnimate(".splash.screen");
		setTimeout(function() {
			$(".splash").css("opacity",0);
		},4000);
		setTimeout(function() {
			enterLocating();
		},4400);
	}
	function enterLocating() {
		$(".locating").css("display","block").css("opacity",1);
		stepAnimate(".locating");
		
		setTimeout(function() {
			$(".radar").addClass("hide");
			$(".station").addClass("active");
		},3000);
	
		setTimeout(function() {
			$(".locating .message").css("opacity",0);
		},4500);
		setTimeout(function() {
			$(".locating .station").html("m Oxshott");
		},4800);
		setTimeout(function() {
			$(".locating .station").html("om Oxshott");
		},4850);
		setTimeout(function() {
			$(".locating .station").html("rom Oxshott");
		},4900);
		setTimeout(function() {
			$(".locating .station").html("From Oxshott");
		},4950);
		setTimeout(function() {
			$(".locating .station").css("transition-duration","1s").css("transition-timing-function",".8,0,.2,1").css("transform","translateY(-220px)");
		},5500);
		setTimeout(function() {
			loadIntro();
		},6500);
		setTimeout(function() {
			$(".locating").css("opacity",0);
		},6700);
		//set timeout something: loadIntro();
	}
	function loadIntro() {
		$(".station_info").css("display","block");
		$(".locating").css("display","none");
		
		setTimeout(function() {
			$(".intro .gfx").css("transform","translateY(0)");
		},300);
		setTimeout(function() {
			$(".controls").css("opacity",1);
		},500);
		$(".station_info .opacityZero").each(function(i, e) {
			setTimeout(function(obj) {
				$(obj).animate({ opacity: 1},300);
			},(i*270)+500,e);
		});
		$(".intro .gfx").css("display","block");
		setTimeout(function() {
			$(".intro .gfx").css("opacity",1);
		},100);

	
		$(".choose_destination").click(function() {
			$(".screen.search_station input.station").focus();
		
				$(".choose_destination").css("transition","all 0.7s cubic-bezier(.85,.01,.99,.38)");
				$(".choose_destination").addClass("disappear");
			
			hideMainBackground();
			setTimeout(function() {
				enterDestinationScreen();
			},700);
		});
	}
	function hideMainBackground() {
		$(".intro .people").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
		setTimeout(function() {
			$(".intro .background").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
		},100);
		setTimeout(function() {
			$(".screen.station_info").animate({ opacity: 0},300).css("pointer-events","none");
		},600);
		$(".station_info .date").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-300px)");
		setTimeout(function() {
			$(".station_info .station").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-300px)");
		},100);
	}
	function enterDestinationScreen() {
		stepAnimate(".search_station");
		$(".search_1 .background").css("transition-duration","0s").css("transition-timing-function","ease").css("transform","translateY(-20px)");
		$(".search_1 .people").css("transition-duration","0s").css("transition-timing-function","ease").css("transform","translateY(5px)");
		setTimeout(function() {
			$(".search_1 .background").css("transition-duration","2s");
			$(".search_1 .people").css("transition-duration","2s");
		},100);
		$(".station_info").css("display","none");
		$(".search_1, .search_1 .gfx").css("display","block");
		setTimeout(function() {
			$(".search_1").css("opacity",1);
		},100);
		setTimeout(function() {
			$(".screen.search_station").css("opacity",0).css("display","block").animate({ opacity: 1},300);
			$(".search_1 .gfx").css("opacity",1);
			$(".search_1").css("transform","translateY(0px)");
		},200);
		setTimeout(function() {
			$(".search_station .results").css("transform","translateY(0px)");
		},350);
	}
	$(".home.screen .info").bind("touchstart",function() {
		closeHomeScreen();
		setTimeout(function() {
			enterJourneyScreen();
		},700);
		
	});
	function rotateInfo() {
		$(".home .info").each(function() {
			var info = $(this);
			if ($("span",info).length>1) {
				$("span:first-child",info).css("transition-duration","0.5s").css("transform","translateY(-42px)");
				$("span:last-child",info).css("transition-duration","0.5s").css("transform","translateY(-42px)");
				setTimeout(function() {
					$("span:last-child",info).prependTo(info);
					$("span:first-child",info).css("transition-duration","0s").css("transform","translateY(0px)");
					$("span:last-child",info).css("transition-duration","0s").css("transform","translateY(0px)");
				},500);
			}
		});
			
		setTimeout(rotateInfo,7000);
	}
	setTimeout(rotateInfo,7000);
});
function closeDestinationScreen() {
	$(".remove").animate({ opacity: 0},200);
	$(".search_1 .people").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
	setTimeout(function() {
		$(".search_1 .background").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
	},100);
	setTimeout(function() {
		$(".search_1").animate({ opacity: 0},300);
	},300);
	setTimeout(function() {
		$(".search_station").animate({ opacity: 0},300);
	},600);
	setTimeout(function() {
		$(".search_station").css("display","none");
	},900);
}
function closeHomeScreen() {
	$(".cards").removeClass("active");
	$(".screen.home .top").css("transition-duration","1s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-500px)").css("opacity",0);
	$(".screen.home .bottom").css("transition-duration","1s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(500px)").css("opacity",0);
	setTimeout(function() {
		$(".screen.home").css("display","none");
	},1200);
}
first=true;
function enterHomeScreen(delay) {
	$(".screen.home .opacityZero").each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).animate({ opacity: 1},300);
		},(i*270)+500,e);
	});
	if (typeof delay == "undefined") delay=1200;
	setTimeout(function() {
		$(".screen.home").css("opacity",0).css("display","block").animate({ opacity: 1},300);
		setTimeout(function() {
			$(".cards").addClass("active");
		},500);
		if (first) {
			clock_slider.Init($(".clock_slider").eq(0),0);
			first = false;
		}
	},(delay));
}
function enterJourneyScreen() {
	$(".journey .opacityZero").each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).animate({ opacity: 1},300);
		},(i*270)+500,e);
	});
	$(".journey").css("display","block").animate({ opacity: 1},300).addClass("active");
	$(".journey ul li:nth-child(4)").addClass("travel");
	$(".journey ul .current_status").addClass("travel");
	$(".journey .date").bind("click",function() {
		closeJourneyScreen();
		enterHomeScreen(0);
		setTimeout(function() {
			$(".screen.home .top").css("transition-duration","1s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
			$(".screen.home .bottom").css("transition-duration","1s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
		},100);
		setTimeout(function() {
			$(".screen.home .top").css("transform","translateY(0)").css("opacity",1);
			$(".screen.home .bottom").css("transform","translateY(0)").css("opacity",1);
		},110);
		// clock_slider.Init($(".clock_slider").eq(1),1);
	
	});
}
function closeJourneyScreen() {
	$(".travel").removeClass("travel");
	$(".journey").css("opacity",0);
	setTimeout(function() {
		$(".journey").css("display","none");
	},300);
}
function stepAnimate(container) {
	$(".opacityZero",$(container)).each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).animate({ opacity: 1},300);
		},(i*270)+500,e);
	});
}