$(function() {
	$(".card").bind("touchstart",function(e) {
		$card=$(e.target);
		if ($card.hasClass("message_1")) {
			activateDelay();
		} else {
			if ($(this).parent().hasClass("open")) {
				$(this).parent().removeClass("open");
			} else {
				$(this).parent().addClass("open");
			}
		}
	});
	function activateDelay() {
		$($card).fadeOut();
		$(".message_2").fadeIn();
		
		$(".card img").attr("src","img/gfx_hand.svg");
		var activeBucket = clock_slider.activeBucket();
		var time_str = ($(".bottomslider span.time").eq(activeBucket).html());
		var time = time_str.split(":");
		var h = parseInt(time[0]);
		var m = parseInt(time[1]);
		m+=22;
		if (m>=60) {
			m-=60;
			h+=1;
		}
		$(".info").animate({opacity:0}, 00);

		var delaydepart = '<span class="delay">Running behind</span>';
		var delayarrive = '<span class="platform">Platform 12</span><span class="delay">Exp '+pad(h,2)+':'+pad(m,2)+'</span>';
		$(".info.depart").html(delaydepart);
		$(".info.arrive").html(delayarrive);
		$(".info").animate({opacity:1},200);
	}
});